import Vue from "vue";
export default Vue.extend({
    data: function () {
        return {
            dat: []
        };
    },
    computed: {
        points: function () {
            return this.dat
                .map(function (item, i) {
                return i + "," + item;
            })
                .join(" ");
        }
    },
    mounted: function () {
        //ランダムデータ
        for (var i = 0; i < 300; i++) {
            this.dat.push((Math.random() + Math.random() + Math.random() + Math.random()) / 4 * 50);
        }
    }
});



